* {
    box-sizing: border-box;
  }
  
  .App-header {
    width: 60%;
    padding: 12px 20px;
    margin: 10px;
    display: inline-block;
    border: none;
    border-radius: 4px;
    box-sizing: border-box;
    background-color: #BAC7BE;
    font-size: 20px;
  }
  
  
  
  input[type=text] {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    
  }
  
  button {
    width: 100%;
    background-color:black;
    color: white;
    padding: 14px 20px;
    margin: 8px 0;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: whitesmoke;
    color: black;
  }